import request from '@/utils/request'
// 发布商品
export function insertProduct(data) {
  return request({
    url: `/product/business/product/insertProduct`,
    method: 'post',
    data
  })
}
// 查询商品编码是否存在
export function isExistProductCode(shopId,code) {
  return request({
    url: `/product/business/product/isExistProductCode?productCode=${code}&shopId=${shopId}`,
    method: 'get',
  })
}
