<!--
  引入：
    import videoMaterial from '@/components/shopMaterial/videoMaterial';
  父：
    <videoMaterial
      :getPost="getPost"
      :multiple="true"
      :totalNum="1"
      :selectNum="1"
      @onlyclose="onlyclose"
      @subMit="subMit"
    ></videoMaterial>

      :limitType="true"  *是否限制了上传数量  Boolean  true支持  false不支持
      :multiple="true"   *是否支持多选  Boolean  true支持  false不支持
      :totalNum="1"      *可选择的总数  Number  
      :selectNum="1"     *已选择数量  Number
      :getPost="true"    *是数据每次打开都刷新
  子：

-->
<template>
  <div>
    <!-- 图片库 -->
    <div class="box">
      <div class="flex-row-start">
        <el-row style="width:100%;" :gutter="20">
          <!-- 左侧操作区域 -->
          <el-col :span="6">
            <div class="materinalOperation">
              <el-card class="box-card">
                <div class="search" style="margin-bottom:20px;">
                  <el-input placeholder="搜索分组" v-model="getGalleryForm.galleryName" class="input-with-select searchInput">
                    <el-button @click="getGaller" slot="append" icon="el-icon-search"></el-button>
                  </el-input>
                </div>
                <div class="class flex-column-center">
                  <div class="classList" :style="{height: OperationHeight + 'px'}" :height="OperationHeight + 'px'">
                    <ul class="infinite-list" style="overflow:auto;height:100%;">
                      <li v-for="tag in imageClassTags" :key="tag.name" @click="activeClass(tag)" class="li infinite-list-item">
                        <el-tag style="width:100%;" :type="tag.type" v-if="tag.id == 0 || tag.id == -1">{{tag.galleryName }} {{tag.id === -1 ? '' : '( ' + tag.totalAmount + ' )' }}</el-tag>
                      </li>
                    </ul>
                  </div>
                </div>
              </el-card>
            </div>
          </el-col>
          <!-- 右侧图片展示区域 -->
          <el-col :span="18">
            <div class="materinalContent">
              <el-card class="box-card" style="margin-bottom:20px">
                <h3 style="margin-bottom:20px;">{{activeClassName}}</h3>
                <div class="flex-row-between">
                  <el-button type="primary" @click="uploadImg=true">上传视频</el-button>
                  <div class="search">
                    <el-input placeholder="搜索视频" v-model="getImgsForm.imageName" class="input-with-select searchInput">
                      <el-button @click="getImgs" slot="append" icon="el-icon-search"></el-button>
                    </el-input>
                  </div>
                </div>
              </el-card>
              <el-card class="box-card">
                <div v-show="imgs.length > 0">
                  <div class="classList" :style="{height: ImageBoxHeight + 'px'}">
                    <ul class="infinite-list flex-row-start1 ul" style="overflow-x: hidden;overflow-y:auto;height:100%;">
                      <li v-for="(item,index) in imgs" :key="index" class="infinite-list-item item" @click="clickItem(item,index)">
                        <div class="demo-image__preview">
                          <el-image style="width: 200px; height: 200px" fit="fill" :src="item.coversUrl"></el-image>
                          <div class="imgNameBox">{{item.imageName}}</div>
                          <div style="text-align:center;color:#909399;">{{item.duration}}</div>
                        </div>
                        <div class="pos" v-show="showPos(item)">{{showNum(item)}}</div>
                      </li>
                    </ul>
                  </div>
                  <div class="fy flex-row-end">
                    <div>
                      <el-pagination
                        @size-change="handleSizeChange"
                        @current-change="handleCurrentChange"
                        :page-sizes="[10, 20, 30]"
                        :page-size="getImgsForm.pageSize"
                        layout="total, sizes, prev, pager, next, jumper"
                        :total="imgsTotal">
                      </el-pagination>
                    </div>
                  </div>
                </div>
                <div v-show="imgs.length == 0">暂无视频，可以点击左上角“上传视频”按钮添加</div>
              </el-card>
            </div>
          </el-col>
        </el-row>
      </div>
      <div class="flex-row-between" style="margin-top:10px;">
        <div v-if="limitType">已选 {{selectNum + selectImgArr.length}} 条，还可以选择 {{totalNum - selectNum - selectImgArr.length}} 条</div>
        <div v-else>已选 {{selectNum + selectImgArr.length}} 条</div>
        <div>
          <el-button @click="onlyClose">取 消</el-button>
          <el-button type="primary" @click="subMit">确 定</el-button>
        </div>
      </div>
    </div>
    <!-- 上传视频 -->
    <el-dialog 
      title="上传视频" 
      :visible.sync="uploadImg" 
      :close-on-press-escape="false"
      :close-on-click-modal="false"
      :show-close="false"
      :modal-append-to-body="false"
      width="70%">
      <el-form :model="uploadImgForm" ref="upload" label-width="100px" class="demo-ruleForm">
        <el-form-item label="本地视频">
          <el-upload class="avatar-uploader"
            :action="action"
            name="files"
            :on-progress="uploadVideoProcess"
            :on-success="handleVideoSuccess"
            :before-upload="beforeUploadVideo"
            :show-file-list="false">
            <video v-if="galleryList[0].imageUrl !='' && !videoFlag"
              :src="galleryList[0].imageUrl"
              style="width:300px;height:200px;"
              class="avatar video-avatar"
              controls="controls">
              您的浏览器不支持视频播放
            </video>
            <el-button v-if="galleryList[0].imageUrl =='' && !videoFlag" type="primary">添加视频</el-button>
            <el-progress v-if="videoFlag == true" type="circle" :percentage="videoUploadPercent" style="margin-top:7px;"></el-progress>
          </el-upload>
          <div>视频不能超过20M，支持mp4，mov，m4v，flv,x-flv，mkv，wmv，avi，rmvb，3gp视频格式</div>
        </el-form-item>
        <el-form-item label="视频标题">
          <el-input style="width:400px;" type="text" placeholder="请输入内容" v-model="uploadImgForm.imageName" maxlength="15" show-word-limit></el-input>
        </el-form-item>
        <el-form-item label="视频封面">
          <el-upload
            class="avatar-uploader"
            :action="imgaction"
            :show-file-list="false"
            :on-success="imgUp"
            name="files"
            :before-upload="beforeAvatarUpload">
            <img style="width:150px;height:150px;" v-if="videoImg" :src="videoImg" class="avatar">
            <el-button v-else type="primary">修改封面</el-button>
          </el-upload>
          <div>默认视频第一帧为封面</div>
        </el-form-item>
        <el-form-item label="选择分组">
          <el-select v-model="uploadImgForm.galleryId" placeholder="请选择">
            <el-option v-for="item in updateClassArr" :key="item.name" :label="item.galleryName" :value="item.id"></el-option>
          </el-select>
        </el-form-item>
        <el-form-item style="text-align:center;">
          <el-button @click="uploadImg = false">关 闭</el-button>
          <el-button :loading="uploadImgLoading" type="primary" @click="uploadImgFun">确 定</el-button>
        </el-form-item>
      </el-form>
    </el-dialog>
  </div>
</template>
<script>
import { mapState } from 'vuex'
import { 
  getGalleryByShopId ,
  getGalleryImageByGroup ,
  addGalleryImage ,
} from '@/api/shop/shopMaterial'
export default {
  props: ["getPost","limitType","multiple","totalNum","selectNum"],
  watch:{
    getPost(newvalue,oldValue){
      if(newvalue){
        console.log('执行了')
        this.selectImgArr = []
        this.getGaller()
      }
    }
  },
  data(){
    return{
      imageClassTags:[],                  // 分组按钮
      getGalleryForm:{},                  // 获取分组数据
      getImgsForm:{                       // 分组下的图片数据
        pageSize:30,
        pageNum:1
      },
      imgs:[],
      imgsTotal:0,
      selectImgs:[],
      uploadImg:false,                    // 上传图片弹出框
      uploadImgLoading:false,             // 上传图片loading
      uploadImgForm:{                     // 上传图片数据
        galleryId:0,
        galleryList:[{}]
      },                   
      videoForm:{
        imageUrl:''
      },
      videoImg:'',
      galleryList:[{imageUrl:''}],                     // 准备上传的图片数据
      activeClassName:'',                 // 默认选中的分组名称
      updateClassArr:[],
      ImageBoxHeight:0,             // 默认数据 - 图片区域高度
      OperationHeight:0,            // 默认数据 - 操作区域高度
      srcList: [],
      action:'',                   // 默认数据 - 上传图片地址
      imgaction:'',
      videoFlag: false,
      //是否显示进度条
      videoUploadPercent: "",
      //进度条的进度，
      isShowUploadVideo: false,
      selectImgArr:[],
    }
  },
  created(){
    // 分组默认数据
    this.getGalleryForm.shopId = this.shop.shopId
    this.getGalleryForm.type = 2
    // 分组下的图片默认数据
    this.getImgsForm.shopId = this.shop.shopId
    this.getImgsForm.type = 2
    this.action = `/product/business/gallery/uploadGalleryVideo?shopId=${this.shop.shopId}`
    this.imgaction = `/product/business/gallery/uploadGalleryImage?shopId=${this.shop.shopId}`
    this.getGaller()
  },
  mounted() {
    this.$nextTick(function() {
      this.OperationHeight = window.innerHeight - 282;
      this.ImageBoxHeight = window.innerHeight - 400;
    })
    window.onresize = () => {
      return (() => {
        this.OperationHeight = window.innerHeight - 282;
        this.ImageBoxHeight = window.innerHeight - 400;
      })();
    };
  },
  methods:{
    // 父 - 关闭
    onlyClose(){
      this.$emit('onlyclose',true);
    },
    // 父 - 确定
    subMit(){
      console.log('this.selectImgArr',this.selectImgArr)
      if(this.selectImgArr.length == 0){
        this.$message.error('请先选择视频');
        return;
      }
      this.$emit('subMit',this.selectImgArr);
    },
    // 点击图片
    clickItem(e){
      if(this.selectImgArr.findIndex(item=>item.id == e.id) === -1){
        if(this.limitType && (this.totalNum - this.selectNum - this.selectImgArr.length <= 0 )){
          this.$message.error('已超过添加最大视频数量');
          return;
        }
        this.selectImgArr.push(e)
      }else{
        this.selectImgArr.splice(this.selectImgArr.findIndex(item => item.id === e.id), 1)
      }
    },
    showPos(e){
      if(this.selectImgArr.findIndex(item=>item.id == e.id) === -1){
        return false;
      }else{
        return true;
      }
    },
    showNum(e){
      return (this.selectImgArr.findIndex(item=>item.id == e.id)) + 1;
    },
     // 图片 - 格式检测
    beforeAvatarUpload(file) {
      console.log('file',file)
      const isJPG = file.type === 'image/jpeg' || file.type === 'image/gif' || file.type === 'image/png' || file.type === 'image/bmp';
      console.log(isJPG)
      const isLt3M = file.size / 1024 / 1024 < 3;

      if (!isJPG) {
        this.$message.error('上传头像图片只能是 JPG 格式!');
      }
      if (!isLt3M) {
        this.$message.error('上传头像图片大小不能超过 2MB!');
      }
      return isJPG && isLt3M;
    },
    // 图片 - 上传
    imgUp(file, fileList){
      console.log('上传封面',file, fileList);
      this.videoImg = file.data[0].name
    },
    // 图片 - 数量限制
    handleExceed(files, fileList) {
      this.$message.warning(`单次最多上传1张图片，本次选择了 ${files.length} 个文件，共选择了 ${files.length + fileList.length} 个文件`);
    },
    //上传前回调
    beforeUploadVideo(file) {
      console.log('上传前',file)
      var fileSize = file.size / 1024 / 1024 < 20;
      if (['video/mp4', 'video/mov', 'video/m4v', 'video/flv', 'video/x-flv', 'video/mkv', 'video/wmv', 'video/avi', 'video/rmvb', 'video/3gp'].indexOf(file.type) == -1) {
        this.$message.error("请上传正确的视频格式");
        return false;
      }
      if (!fileSize) {
        this.$message.error("视频大小不能超过20MB");
        return false;
      }
      this.isShowUploadVideo = false;
    },
    //进度条
    uploadVideoProcess(event, file, fileList) {
      console.log('进度条',event,file,fileList)
      this.videoFlag = true;
      this.videoUploadPercent = file.percentage.toFixed(0) * 1;
    },
    //上传成功回调
    handleVideoSuccess(res, file) {
      console.log('上传成功',res,file)
      this.isShowUploadVideo = true;
      this.videoFlag = false;
      this.videoUploadPercent = 0;
      //后台上传地址
      if (res.code == 200) {
        console.log('this.uploadImgForm.galleryList',this.uploadImgForm.galleryList,this.uploadImgForm.galleryList[0].coversUrl == '')
        let arr = []
        let obj = {
          imageUrl:res.data[0].name,
          duration:res.data[0].duration,
          imageSize:res.data[0].size
        }
        if(!this.videoImg) this.videoImg = res.data[0].coversUrl
        console.log('obj',obj)
        arr.push(obj)
        this.galleryList = arr
      } else {
        this.$message.error(res.Message);
      }
    },
    // 图片 - 获取分组下图片列表
    getImgs(){
      getGalleryImageByGroup(this.getImgsForm).then(res=>{
        console.log('获取分类下图片',res)
        let data = res.data.list;
        data.forEach(e=>e.checked = false)
        this.imgs = data;
        this.imgsTotal = res.data.total;
      })
    },
    // 图片 - 点击图片放大
    showBigImg(e){
      this.videoPlayShow = true;
      this.videoPlay.imageUrl = e
    },
    // 图片 上传请求
    uploadImgFun(){
      console.log('this.uploadImgForm',this.uploadImgForm)
      if(!this.galleryList[0].imageUrl){
        this.$message.error('请先上传视频');
        return;
      }
      if(!this.uploadImgForm.imageName){
        this.$message.error('请为视频命名');
        return;
      }
      this.galleryList[0].imageName = this.uploadImgForm.imageName
      this.galleryList[0].coversUrl = this.videoImg
      this.uploadImgForm.galleryList = this.galleryList
      this.uploadImgForm.type = 2 
      this.uploadImgForm.shopId = this.shop.shopId 
      this.uploadImgLoading = true;
      console.log('请求上传的接口',this.uploadImgForm)
      this.selectImgArr = this.galleryList
      addGalleryImage(this.uploadImgForm).then(res=>{
        console.log('上传视频保存',res)
        if(res.code == 200){
          this.$message({
            message: '恭喜，上传成功',
            type: 'success'
          });
          this.getGaller();
          this.uploadImgForm.galleryId = 0
          this.uploadImgForm.imageName = ''
          this.uploadImgForm.coversUrl = ''
          this.videoImg = ''
          this.subMit()
          this.galleryList = [{
            imageUrl:''
          }]
          this.uploadImg = false;
          setTimeout(()=>{
            this.uploadImgLoading = false;
          },1000)
        }else{
          this.uploadImgLoading = false;
          this.$message.error(res.message);
        }
      })
    },
    // 分类 - 获取图片库分类
    getGaller(){
      console.log('请求分类的数据',this.getGalleryForm)
      getGalleryByShopId(this.getGalleryForm).then(res=>{
        console.log('获取视频库分组',res)
        let data = res.data;
        data.forEach(e=>{ e.type = 'info' })
        if(this.activeClassName == ''){
          this.activeClassName = data[0].galleryName
          data[0].type = ''
          this.getImgsForm.galleryId = data[0].id;
        }else{
          let index = data.findIndex(item => item.galleryName == this.activeClassName);
          console.log('index',index)
          data[index].type = ''
          this.getImgsForm.galleryId = data[index].id;
        }
        let arr = []
        for(var i=1;i<data.length;i++){
          arr.push(data[i])
        }
        this.updateClassArr = arr;
        this.changeClassArr = arr;
        console.log('arr',arr)
        this.getImgs()
        this.imageClassTags = data
      })
    },
    // 分类 - 选择当前组
    activeClass(e){
      console.log('点击了分组按钮')
      this.imageClassTags.forEach(el => el.type = 'info')
      e.type  = ''
      this.selectImgs = [];
      this.allChecked = false;
      this.getImgsForm.galleryId = e.id
      this.activeClassName = e.galleryName;
      this.getImgsForm.imageName = ''
      this.getImgs()
    },

    // 图片 - 分页
    handleSizeChange(val) {
      this.getImgsForm.pageSize = val;
      this.getImgs()
    },
    // 图片 - 分页
    handleCurrentChange(val) {
      this.getImgsForm.pageNum = val;
      this.getImgs()
    },
    // 默认操作方法
    // 文本框输入
    change(e){
      this.$forceUpdate()
    }
  },
  computed: {
    ...mapState([
      'user',
      'shop'
    ])
  },
}
</script>
<style lang="less" scoped>
.curImg{
  cursor: pointer;
}
.materinalOperation{
  max-width: 400px;
}
.box{
  box-sizing: border-box;
  width: 100%;
  padding:20px;
  border:1px solid #eee;
  border-radius: 4px;
}
.search{
  position: relative;
  text-align: center;
  .searchInput{
    width: 200px;
  }
  .searchBtn1{
    position: absolute;
    color:rgb(220, 223, 230);
    right: 8px;
    top: 0;
    &.activeBtn{
      color: #409EFF;
    }
  }
}
.addClass{
  margin-bottom: 20px;
}
.classList{
  width: 100%;
  text-align: center;
  .li{
    cursor: pointer;
    margin-bottom: 10px;
    .tagBtn{
      .tagInfo{
        position: relative;
        .tag{
          z-index: 9999;
          width: 18px;
          height: 18px;
          text-align: center;
          line-height: 18px;
          // display: none;
          position: absolute;
          top: -20px;
          &.close{
            right: -28px;
          }
          &.edit{
            right: 4px;
          }
        }
      }
      &:hover .tag{
        display: block;
      }
    }
  }
  .ul{
    flex-wrap: wrap;
  }
  .item{
    margin: 20px 20px 0 0 ;
    width: 200px;
    position: relative;
    .imgNameBox{
      width: 100%;
      text-align: center;
      display:-webkit-box;
      -webkit-box-orient:vertical;
      -webkit-line-clamp:1;/*我设置的是文字超过1行显示...；行数可以随便改；*/
      overflow:hidden;
    }
    .pos{
      position: absolute;
      left: 0;
      top: 0;
      width: 100%;
      height: 100%;
      background-color: rgba(0,0,0,.7);
      color: #fff;
      text-align: center;
      line-height: 248px;
      font-size: 100px;
      font-weight: bold;
    }
  }
}
</style>
<style scoped>
  .classList /deep/ .el-scrollbar__bar.is-horizontal{
    display: none;
  }
  .el-checkbox /deep/ .el-checkbox__label {
    width: 186px;
    white-space:nowrap;
    overflow:hidden;
    text-overflow:ellipsis;
    vertical-align: top;
  }
</style>